export default {
	data() {
		return {
			news:[]
		};
	},
	onLoad:function(){
		uni.showLoading({
			title: '正在加载...',
			mask: false
		})
		uni.request({
			method:'GET',
			url:'https://unidemo.dcloud.net.cn/api/news',
			data:{},
			success: res =>{
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: ()=>{},
			complete: () => {
				
			}
			
		});
	},
	methods: {
		showDetail(e) { 
			console.log(e);
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
}
